@import url('https://webfontworld.github.io/tmoney/TmoneyRoundWind.css');

// #4675C0  #8FC8EB  #B1BEF2

:root{
  --white: #fff;
  --gray_f023: #f0f2f3;
  --gray_e: #eee;
  --gray_5: #555;
  --black: #222;

  --blue: #4675c0;
}

* {
  box-sizing: border-box;
  color: var(--black);
  font-family: 'TmoneyRoundWind';
  &:focus-visible{
    outline: none;
  }
}
::-webkit-scrollbar{
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-track{
  background: var(--gray_e5);
  border-radius: 6px;
}
::-webkit-scrollbar-thumb{
  background: var(--blue);
  border-radius: 6px;
}

body{
  min-width: 320px;
  margin: 0;
  p, h1{
    margin-block-start: 0;
    margin-block-end: 0;
  }
  ul{
    list-style-type: none;
    margin-block-start: 0;
    margin-block-end: 0;
    padding-inline-start: 0;
  }
  a{
    text-decoration: none;
  }
}